<template>
  <!--全部订单列表-->
  <div id="myOrder" class="left">
    <home-header :title="$route.query.title" :back="true"></home-header>
    <div class="listImg">
      <ul class="orderList">
        <li v-for="(item,index) in orderlist" :key="index">
          <a @click="orderShip(index)">
            <img :src="item.img" alt=""/>
            <dl>
              <dt><h4>订单号：{{item.id}}</h4></dt>
              <dd>订单状态：{{item.status}}</dd>
              <dd>
                订单总金额：
                <span>¥{{item.paymentActual}}元</span>
              </dd>
              <dd>下单时间：{{item.createTime |filterTime}}</dd>
            </dl>
            <i class="iconfont icon-arrow-right-copy"></i>
          </a>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  import homeHeader from '../../components/header/header.vue'
  import {filterTime} from "../../mixins/filter";
  export default {
    name: 'carrousel',
    data() {
      return {
        orderlist:null,
      }
    },
    filters: {
      filterTime: filterTime
    },
    computed: {},
    components: {
      homeHeader,
    },
    created() {
      this.listTab();
    },
    methods: {
      listTab() {
        let that=this;
        var memberId=window.localStorage.getItem('memberId');
        if (that.$route.query.title == "全部订单") {
            that.ajax({
              url:'/order/orderlistall',
              method:'post',
              data:{
                memberId:memberId
              },
              success:function(res){
                if(res.status==1000){
                  console.log(2)
                  that.orderlist=res.data;
                  for (var i = 0; i < that.orderlist.length; i++) {
                     var item = that.orderlist[i];
                     if (item.status == "10") {
                       item.status = "待支付";
                     }else if(item.status == "30"){
                       item.status = "待发货";
                     }else if(item.status == "60"){
                       item.status = "待收货";
                     }
                  }
                }
              }
            })
        } else if (that.$route.query.title == "待付款") {
          that.ajax({
            url:'/order/orderlistbystatus',
            method:'post',
            data:{
              memberId:memberId,
              status:10
            },
            success:function(res){
              if(res.status==1000){
                console.log(2)
                that.orderlist=res.data;
                for (var i = 0; i < that.orderlist.length; i++) {
                  var item = that.orderlist[i];
                  if (item.status == "10") {
                    item.status = "待支付";
                  }
                }
              }
            }
          })
        } else if (that.$route.query.title == "待发货") {
          that.ajax({
            url:'/order/orderlistbystatus',
            method:'post',
            data:{
              memberId:memberId,
              status:30
            },
            success:function(res){
              if(res.status==1000){
                console.log(2)
                that.orderlist=res.data;
                for (var i = 0; i < that.orderlist.length; i++) {
                  var item = that.orderlist[i];
                  if (item.status == "30") {
                    item.status = "待发货";
                  }
                }
              }
            }
          })
        } else if (that.$route.query.title == "待收货") {
          that.ajax({
            url:'/order/orderlistbystatus',
            method:'post',
            data:{
              memberId:memberId,
              status:60
            },
            success:function(res){
              if(res.status==1000){
                console.log(2)
                that.orderlist=res.data;
                for (var i = 0; i < that.orderlist.length; i++) {
                  var item = that.orderlist[i];
                  if (item.status == "60") {
                    item.status = "待收货";
                  }
                }
              }
            }
          })
        } else {
          that.orderlist = that.orderlist1;
        }
      },
      orderShip(val) {
        var status=this.orderlist[val].status;
        if(status=='待支付'){
          this.$router.push({path: '/waitPay', query: {orderNumber:this.orderlist[val].id}})
        }else{
          this.$router.push({path: '/ship', query: {orderNumber: this.orderlist[val].id}})
        }
      }
    }
  }
</script>

<style lang="scss">
  @import "../../style/minxin";

  $b: #f0f0f0;

  #myOrder {
    .listImg {
      width: 100%;
      box-sizing: border-box;
      width: 100%;
      padding: 2.14rem 0 0;
      .orderList {
        padding-top: 0.6rem;
        li {
          box-sizing: border-box;
          width: 100%;
          height: auto;
          position: relative;
          overflow: hidden;
          padding: 0.6rem;
          border-bottom: 1px #e3e3e3 dotted;
          a {
            display: block;
            text-decoration: none;
            font-style: normal;
            color: #333;
            img {
              float: left;
              width: 7rem;
              height: 6rem;
            }
            dl {
              margin-left: 7.4rem;
              dt {
                font-weight: 700;
                h4 {
                  font-size: 1rem;
                  line-height: 1.5rem;
                  overflow: hidden;
                }
              }
              dd {
                margin-top: 0.3rem;
                font-size: 0.8rem;
                span {
                  color: $red !important;
                }
              }
            }
            i {
              position: absolute;
              top: 53%;
              font-size: 2rem;
              color: #bbb;
              margin-top: -1.2rem;
              right: 0;
            }
          }
        }
      }
    }
  }

</style>



